function mainForms() {

//    

// ,      
let messageForm = {
    loadingForm: '...', //  
    successForm: ',     !  !', //    
    failureForm: '-   ...' //  
};

let contactForm = document.getElementsByClassName('forms'), //    
    formInputText = document.querySelectorAll('input[type=text]'), //    
    formInputTel = document.querySelectorAll('input[type=tel]'), //     
    contactMessage = document.createElement('div'), //       
    formData; //  ,    

    //      
    contactMessage.classList.add('status');

//  ,   ,  
formInputTel.forEach( (elem) => {
    //    11      
    elem.setAttribute('maxlength', '11');
    elem.addEventListener('input', function() {
        this.value = this.value.replace(/[^\d(?:+)]/ig, '');
    });
});
//      
function clearInputForm() {
    //    
    for (let i = 0; i < formInputText.length; i++) {
        formInputText[i].value = '';
    } //      
    for (let i = 0; i < formInputTel.length; i++) {
        formInputTel[i].value = '';
    }
}

//        
window.addEventListener('submit', function(event) {
    let target = event.target;
    event.preventDefault();
        if (target && target.classList.contains('form')) {
            for (let i = 0; i < contactForm.length; i++) {
                if (target == contactForm[i]) {
                    formData = contactForm[i]; //     
                    formData.appendChild(contactMessage); //   
                    postDataForm() //    
                        .then( () => contactMessage.innerHTML = messageForm.loadingForm)
                        .then( () => contactMessage.innerHTML = messageForm.successForm)
                        .catch( () => contactMessage.innerHTML = messageForm.failureForm)
                        .then(clearInputForm); //    
                    break;
            }
        }
    }
});

//    ,    ajax  
function postDataForm() {

    return new Promise(function(resolve, reject) {
        let contactFormData = new FormData(formData);
        let objForm = {}; //      
        contactFormData.forEach(function(value, key) {
            objForm[key] = value; //    -  
        }); //   json 
        let json = JSON.stringify(objForm);
            let request = new XMLHttpRequest();
            request.open('POST', 'server.php'); //  
            request.setRequestHeader('Content-Type', 'application/json; charset=utf-8');  
            request.onreadystatechange = function() {
                if (request.readyState < 4) {
                    resolve();
                } else if (request.readyState === 4) {
                    if (request.status == 200 && request.status < 3) {
                        resolve();
                    }
                    else {
                        reject();
                    }
                }
            };
                request.send(json);
        });
    }

}

module.exports = mainForms;